import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { connect } from 'react-redux';
import {
  Button,
  Card,
  CardBody,
  CustomInput,
  FormGroup,
  Input,
  Label,
  Row,
} from 'reactstrap';
import {
  Colxx,
  Separator,
} from '../../../../components/common/CustomBootstrap';
import { adminRoot } from '../../../../constants/defaultValues';
import { NotificationManager } from '../../../../components/common/react-notifications';
import Breadcrumb from '../../../../containers/navs/Breadcrumb';
import useRequest from '../../../../hooks/use-request';

const Submission = ({ match, currentUser }) => {
  const history = useHistory();
  const { id } = match.params;

  const [values, setValues] = useState({
    hackathon_id: id,
    user_id: currentUser.id,
    url: '',
    files: null,
    video_file: null,
  });

  const formData = new FormData();

  const handleChange = (event, key, value = false) => {
    setValues({
      ...values,
      [key]: value ? event : event.target.value,
    });
  };

  const handleFileUpload = (event) => {
    setValues({ ...values, [event.target.id]: event.target.files[0] });
  };

  const [doRequest, loading] = useRequest({
    url: '/submissionHackthon',
    method: 'POST',
    data: formData,
    nextSuccess: () => {
      NotificationManager.success(
        'Solution Submitted Successfully!',
        'Hackathon Submission Success',
        3000,
        null,
        null,
        ''
      );
      history.push(`${adminRoot}/challenges/${id}`);
    },
  });

  const onSubmit = () => {
    if ((values.url === '' || values.files === null, values.video_file === ''))
      return;

    formData.append('hackathon_id', values.hackathon_id);
    formData.append('user_id', values.user_id);
    formData.append('url', values.url);
    formData.append('files', values.files);
    formData.append('video_file', values.video_file);

    doRequest();
  };

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.submission" />
          <Separator className="mb-5" />
        </Colxx>
      </Row>

      <Row className="justify-content-center">
        <Colxx xxs="8" className="mb-4">
          <Card>
            <CardBody>
              <FormGroup>
                <Label>
                  <h2 className="lead">Submission</h2>
                </Label>
              </FormGroup>
              <FormGroup>
                <Label>Solution Link</Label>
              </FormGroup>
              <FormGroup>
                <Input
                  id="url"
                  value={values.url}
                  onChange={(e) => handleChange(e, 'url')}
                />
              </FormGroup>

              <FormGroup>
                <Label>Solution Video Link</Label>
              </FormGroup>

              <FormGroup>
                <Input
                  id="video_file"
                  value={values.video_file}
                  onChange={(e) => handleChange(e, 'video_file')}
                />
              </FormGroup>

              <FormGroup>
                <Label>Solution Zip</Label>
              </FormGroup>
              <FormGroup className="mb-4">
                <CustomInput
                  id="files"
                  value=""
                  type="file"
                  onChange={handleFileUpload}
                />
              </FormGroup>

              <Row className="justify-content-end">
                <FormGroup className="px-4">
                  <Button
                    color="primary"
                    className={`btn-shadow btn-multiple-state ${
                      loading ? 'show-spinner' : ''
                    }`}
                    size="lg"
                    onClick={() => onSubmit()}
                  >
                    <span className="spinner d-inline-block">
                      <span className="bounce1" />
                      <span className="bounce2" />
                      <span className="bounce3" />
                    </span>
                    <span className="label">Submit Solution</span>
                  </Button>
                </FormGroup>
              </Row>
            </CardBody>
          </Card>
        </Colxx>
      </Row>
    </>
  );
};

const mapStateToProps = ({ authUser }) => {
  const { currentUser } = authUser;
  return { currentUser };
};
const mapActionsToProps = {};

export default connect(mapStateToProps, mapActionsToProps)(Submission);
